	  display: flex;
	  align-items: center;
	  border: 1px solid $border;
	  background-color:$gray-100;
	  padding: 10px 5px;
	  color: $gray-900;
	  font-size: 13px;
	  font-weight: 600;
  
	  > i {
		font-size: 32px;
		line-height: 0;
		margin-inline-end: 10px;
		opacity: .5;
	  }
	}
  
	.select2-container {
	  width: 100% !important;
	}
  }
  
  .main-modal-calendar-event {
	.modal-content {
	  border-width: 0;
	  position: relative;
	  background-color: transparent;
  
	  > .btn.close {
		font-size: 18px;
		text-shadow: none;
	  }
	}
  
	.modal-header {
	  position: relative;
	  padding: 20px;
	  border-bottom-width: 0;
	  display: flex;
	  align-items: center;
	  justify-content: space-between;
	  border-start-start-radius: 0;
	  border-start-end-radius: 0;
	}
  
	.nav-modal-event {
	  align-items: center;
  
	  .nav-link {
		padding: 0;
		color: rgba(255, 255, 255, 0.75);
		font-size: 21px;
		line-height: 0;
		position: relative;
		transition: all 0.2s ease-in-out;
  
		&:hover, &:focus {
		  color: $white;
		}
  
		+ .nav-link {
		  margin-inline-start: 15px;
		}
  
		&:last-child {
		  font-size: 24px;
		}
	  }
	}
  
	.event-title {
	  color: $white;
	  font-size: 18px;
	  font-weight: 500;
	  margin-block-end: 0;
